.lab {
  padding: 20px 15px;
  font-size: .875rem;

  &__title {
	text-align: center;
  }

  &__goal {
	font-size: 1rem;

	&-title {
	  font-weight: bold;
	}
  }

  &__work {
	&-title {
	  font-weight: bold;
	}
  }

  &__result {

	&-title {
	  font-style: italic;
	  font-weight: bold;
	  background: #f3f3f3;
	  padding: 5px 10px;
	  margin-bottom: 10px;
	}

	&-blocks {
	  width: 100%;
	  //display: flex;
	  //align-items: flex-start;
	}

	&-block {
	  flex-grow: 1;
	  width: 100%;
	  overflow-y: hidden;
	  overflow-x: auto;

	  &-title {
		font-style: italic;
		text-decoration: underline;
		text-underline-position: under;
	  }
	}

	&-plots {
	  width: 100%;
	  display: flex;
	  flex-direction: column;
	  margin-top: 5px;

	  &_direction {
		&_row {
		  flex-direction: row;
		}
	  }

	  &_justify_space-between {
		justify-content: space-between;
	  }
	}

	&-plot {
	  max-height: 270px;
	}
  }

  &__table {
	&-th, &-td {
	  text-align: center;

	  &_width {
		@for $i from 1 through 10 {
		  &_#{$i} {
			width: 100% / $i;
		  }
		}
	  }
	}

	&-th, &-td {
	  &_type {
		&_formula {
		  padding: 10px 0;
		}
	  }
	}

	&-td {
	  &_type_result {
		background: lightgreen;
	  }
	}

	&-th {
	  font-weight: bold;
	  * {
		font-weight: bold;
	  }
	}

	&-input {
	  &_no-border {
		border: none;
	  }

	  text-align: center;
	}
  }

  &__conclusion {
	&-title {
	  font-weight: bold;
	}
  }
}
