.nav {
  &__list {
	padding: 0;
	margin: 0;
	list-style: none;
	display: flex;
  }

  &__item {
	min-height: 30px;
	padding: 0 15px;
	background: #f7f7f7;

	&:hover {
	  background: #e7e7e7;
	}

	&-link {
	  width: 100%;
	  height: 100%;
	  display: flex;
	  align-items: center;
	  justify-content: center;
	  text-decoration: none;
	  color: #242424;
	}
  }
}
